import React from 'react'
import OrderList from './../components/Order/OrderListRow';
import Link from 'next/link';

export default function orders() {
  return (
    <div className="row">
        <div className="col-12 grid-margin">
            <div className="card">
                <div className="card-header">
                    <button>ADD BOOK</button>
                </div>
                <div className="card-body">
                    <div className="form-sample">
                        <div className="row">
                            <div className="table-responsive">
                                <table className="table">
                                    <thead>
                                        <tr>
                                            <th>#ORDER ID</th>
                                            <th>Customer Name</th>
                                            <th>Progress</th>
                                            <th>Next Action</th>
                                            <th>Address</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <OrderList/>
                                        <OrderList/>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <br/><br/>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}
